@font-face
{
    font-family: 'GothamPro-Bold';
    font-weight: normal;
    font-style: normal;

    src: url('../fonts/GothamPro-Bold.eot');
    src: url('../fonts/GothamPro-Bold.woff2') format('woff2'), url('../fonts/GothamPro-Bold.woff') format('woff'), url('../fonts/GothamPro-Bold.ttf') format('truetype'), url('../fonts/GothamPro-Bold.svg#GothamPro-Bold') format('svg'), url('../fonts/GothamPro-Bold.eot?#iefix') format('embedded-opentype');
}
.logo
{
    font-size: 0;

    background: url(../../img/logo.png) no-repeat center;
    -webkit-background-size: 100% 100%;
            background-size: 100%;
}
header
{
    font-size: 1rem;

    padding: 2rem;

    color: #fff;
    background-color: #007b79;
}
header .logo
{
    height: 8rem;
    padding: 0;
    /*width: 19rem;*/
}
header .logo a
{
    display: inline-block;

    width: 100%;
    height: 100%;
}
header .details
{
    margin-top: 2rem;
    padding: .35rem 1.28rem;

    border-left: .14rem solid #fff;
}
header .details a
{
    color: #fff;
}
.screen-one
{
    font-size: 1.3rem;

    padding: 2rem 0;

    text-align: center;

    background: url(../../img/screen-one-bg.jpg) no-repeat top;
    background-attachment: fixed;
}
.screen-one h1
{
    font-family: 'GothamPro-Bold';
    font-size: 2.2rem;
    line-height: 1.3;

    margin-top: 8rem;
    margin-bottom: 4rem;

    text-transform: uppercase;

    color: #fff;
}
.screen-one .input-group-addon-primary
{
    color: #fff;
    background-color: #007b79;
}
caption
{
    padding: 0;
}
caption h3
{
    font-family: 'GothamPro-Bold';
    font-size: 24px;

    margin: 0;
    padding: 33px;

    text-align: center;
    text-transform: uppercase;

    color: #fff;
    background-color: #007b79;
}
.alfa-table
{
    margin-bottom: 0;
}
.alfa-table td
{
    font-size: 25px;

    height: 85px;
    padding: 2rem;
}
thead tr td
{
    background-color: #e3e4e7;
}
.counseling caption h3
{
    background-color: #b7b9b9;
}
.counseling tr:last-child td
{
    text-align: center;
}
.accompaniment table tr td
{
    text-align: center;
}
.footnote
{
    background-color: #007b79;
    background-image: url(../../img/footnote-bg.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 7% center;
}
.card
{
    font-size: 2rem;

    margin-top: 5rem;
    margin-bottom: 5rem;
    padding: 7%;

    border: .7rem solid #3d5765;
    background-color: #fff;
}
.card p
{
    margin: 0 0 56px 44px;
}
.card ul li
{
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         flex;

    margin-bottom: 20px;

    list-style-type: none;
}
.card ul li:before
{
    padding-right: 2rem;

    content: '';

    color: red;
}
.narrow p
{
    margin: 0 0 1.4rem;
}
.note
{
    font-size: 2rem;

    margin-top: 5rem;
    margin-bottom: 5rem;
}
.note p
{
    font-weight: bold;

    padding: 1rem 3rem;

    border-left: .6rem solid #f00;
}
.fill-color-bg
{
    padding: 1px;

    background-color: #007b79;
}
footer
{
    padding: 5rem;

    background: #007b79;
}
footer a
{
    color: #00aeab;
}
footer a:hover
{
    color: #fff;
}
footer .note
{
    margin: 0;
}
footer .note p
{
    font-family: 'GothamPro-Bold';
    font-size: 2rem;

    margin: 0;
    padding: 0 2rem;

    text-transform: uppercase;

    color: #fff;
    border-color: #fff;
}
footer .logo
{
    height: 7rem;

    background-position: center;
    -webkit-background-size: 100% 100%;
            background-size: 100%;
}
